export default class FloatButton {
    constructor(el){
        this.root = el
        if(!this.root) return false
        this._init();
    }

    _init(){
        this._eventHandler();
    }
    _eventHandler(){
        let footer = document.querySelector('footer.footer')
        let observer = new IntersectionObserver( 
        ([e]) => {
            e.intersectionRatio > 0? this.root.classList.add('is-bottom'): this.root.classList.remove('is-bottom')
            },
        {threshold: [0]}
        );

        observer.observe(footer)
    }
}